import React, { useState } from "react";
import Dropzone from "react-dropzone";
import { Form, Input, Icon } from "antd";
import Axios from "axios";
import { useSelector } from "react-redux";
import "../../utils/Hashtag.css";
const { TextArea } = Input;

const Genres = [
  { key: 1, value: " & " },
  { key: 2, value: " " },
  { key: 3, value: "" },
  { key: 4, value: "" },
  { key: 5, value: "" },
  { key: 6, value: "" },
  { key: 7, value: "" },
];

function UploadProductPage(props) {
  const user = useSelector((state) => state.user);
  const [Title, setTitle] = useState("");
  const [Description, setDescription] = useState("");
  const [Price, setPrice] = useState(0);
  const [Genre, setGenre] = useState(1);
  const [Images, setImages] = useState([]);
  const [FilePath, setFilePath] = useState("");
  const [Duration, setDuration] = useState("");
  const [ThumbnailPath, setThumbnailPath] = useState("");
  const [S3thumbnailPath, setS3thumbnailPath] = useState("")
  const [Tags, setTags] = useState([]);
  const [progress, setProgress] = useState(0);

  const removeTags = (indexToRemove) => {
    setTags(Tags.filter((_, index) => index !== indexToRemove));
  };
  const addTags = (event) => {
    if (event.keyCode === 32 && event.target.value !== "") {
      setTags([...Tags, event.target.value.trim()]); // 
      event.target.value = "";
    }
  };

  const titleChangeHandler = (event) => {
    setTitle(event.currentTarget.value);
  };

  const descriptionChangeHandler = (event) => {
    setDescription(event.currentTarget.value);
  };

  const priceChangeHandler = (event) => {
    setPrice(event.currentTarget.value);
  };

  const genreChangeHandler = (event) => {
    setGenre(event.currentTarget.value);
  };

  const updateImages = (newImages) => {
    setImages(newImages);
  };
  
  const resetHandler = (event) => {
    event.preventDefault();
    setImages([]);
    setFilePath("");
    setDuration("");
    setThumbnailPath("");
    setTitle("");
    setDescription("");
    setPrice(0);
    setGenre(1);
    setTags([]);
    setProgress(0);
    alert(".");
  }
  
  const dropHandler = (files) => {
    setProgress(0);
    let formData = new FormData();
    const config = {
      header: { "content-type": "multipart/form-data"},
      //   config 
      onUploadProgress: progressEvent => {
        setProgress(
          parseInt (
            Math.round((progressEvent.loaded * 100) / progressEvent.total)
          )
        );
      }
    };
    
    formData.append("file", files[0]);

    Axios.post("/api/product/video", formData, config).then((response) => {
      if (response.data.success) {
        let variable = {
          filePath: response.data.filePath,
          fileName: response.data.fileName,
        };

        setFilePath(response.data.s3VideoPath);

        Axios.post("/api/product/thumbnail", variable).then((response) => {
          if (response.data.success) {
            setDuration(response.data.fileDuration);
            setThumbnailPath(response.data.filePath);
            setS3thumbnailPath(response.data.s3FilePath)
            setImages((Images) => [...Images, response.data.filePath]);
          } else {
            setProgress(0);
            alert("  .");
          }
        });
      } else {
        if(response.data.err === 'not allowed format'){
          alert("  .")
        } else {
          alert("  .");
        }
        setProgress(0);
      }
    });
  };

  const submitHandler = (event) => {
    event.preventDefault();

    if (!Title || !Description || !Price || !Genre || Images.length === 0) {
      return alert("    .");
    }

    //   request .
  
    const body = {
      //   ID
      writer: user.userData._id,
      title: Title,
      description: Description,
      price: Price,
      images: Images,
      genres: Genre,
      filePath: FilePath,
      duration: Duration,
      thumbnail: ThumbnailPath,
      s3thumbnail: S3thumbnailPath,
      tags: Tags,
    };

    Axios.post("/api/product", body).then((response) => {
      if (response.data.success) {
        alert("   .");
        props.history.push("/");
      } else {
        alert("   .");
      }
    });
  };

  // const deleteHandler = (image) => {
  //   const currentIndex = Images.indexOf(image);
  //   let newImages = [...Images];
  //   newImages.splice(currentIndex, 1);
  //   setImages(newImages);
  // };

  return (
    <div style={{ maxWidth: "700px", margin: "2rem auto" }}>
      <div style={{ textAlign: "center", marginBottom: "2rem" }}>
        <h2>   </h2>
      </div>

      <Form onSubmit={submitHandler}>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          {/* DropZone */}
          <Dropzone
            onDrop={dropHandler}
            multiple={false}
            // maxSize={100000000}
            refreshFunction={updateImages}
          >
            {({ getRootProps, getInputProps }) => (
              <div
                style={{
                  width: 336,
                  height: 189,
                  border: "1px solid lightgray",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
                {...getRootProps()}
              >
                <input {...getInputProps()} />
                <Icon type="plus" style={{ fontSize: "3rem" }} />
              </div>
            )}
          </Dropzone>

          {/* <div
            style={{
              display: "flex",
              width: "350px",
              height: "240px",
              overflowX: "scroll",
            }}
          >
            {Images.map((image, index) => (
              <div onClick={() => deleteHandler(image)} key={index}>
                <img
                  style={{ minWidth: "300px", width: "300px", height: "240px" }}
                  src={`http://localhost:5000/${image}`}
                />
              </div>
            ))}
          </div> */}

          {ThumbnailPath && ( //ThumbnailPath   
            <div>
              <img
                src={`http://localhost:5000/${ThumbnailPath}`}
                alt="thumbnail"
              />
            </div>
          )}

          {/*  (  X ) */}
          {/* {S3thumbnailPath && ( //ThumbnailPath   
            <div>
              <img
                src={`${S3thumbnailPath}`}
                alt="thumbnail"
              />
            </div>
          )} */}
        </div>
        <br />
        <label></label>
        <Input onChange={titleChangeHandler} value={Title} />
        <br />
        <br />
        <label></label>
        <TextArea onChange={descriptionChangeHandler} value={Description} />
        <br />
        <br />
        <label>()</label>
        <Input type="number" onChange={priceChangeHandler} value={Price} />
        <br />
        <label></label>
        <br />
        <select onChange={genreChangeHandler} value={Genre}>
          {Genres.map((item) => (
            <option key={item.key} value={item.key}>
              {" "}
              {item.value}
            </option>
          ))}
        </select>
        <br />
        <label></label>
        <br />
        <div className="box">
          <div className="tags-input">
            <ul>
              {Tags.map((tag, index) => (
                <li key={index} className="tag">
                  <span>{tag}</span>
                  <span
                    className="tag-close-icon"
                    onClick={() => removeTags(index)}
                  >
                    X
                  </span>
                </li>
              ))}
            </ul>
            <input
              type="text"
              placeholder="   "
              onKeyUp={(e) => (e.keyCode === 32 ? addTags(e) : null)}
            />
          </div>
        </div>
        <button onClick = {resetHandler}></button>
        <button type="submit"></button>
      </Form>
    </div>
  );
}

export default UploadProductPage;
