import React, { useEffect, useState } from "react";
import axios from "axios";
import { Avatar, Icon, Col, Card, Row, Carousel } from "antd";
import Meta from "antd/lib/card/Meta";
import Checkbox from "./Sections/CheckBox";
import Radiobox from "./Sections/RadioBox";
import SearchFeature from "./Sections/SearchFeature";
import { genres, price } from "./Sections/Datas";

function UserVideos(props) {
  const userId = props.match.params.userId;

  const [Products, setProducts] = useState([]);
  const [Skip, setSkip] = useState(0);
  const [Limit, setLimit] = useState(8);
  const [PostSize, setPostSize] = useState(0);
  const [Filters, setFilters] = useState({
    genres: [],
    price: [],
  });
  const [SearchTerm, setSearchTerm] = useState("");
  //  getProducts !
  useEffect(() => {
    let body = {
      skip: Skip,
      limit: Limit,
    };

    getProducts(body);
  }, []);

  //  
  const getProducts = (body) => {
    axios
      .post(`/api/product/products_by_userId?userId=${userId}`, body)
      .then((response) => {
        if (response.data.success) {
          if (body.loadMore) {
            setProducts([...Products, ...response.data.productInfo]);
          } else {
            setProducts(response.data.productInfo);
          }
          setPostSize(response.data.postSize);
        } else {
          alert("    .");
        }
      });
  };

  //    getProducts !
  const loadMoreHanlder = () => {
    let skip = Skip + Limit;
    let body = {
      skip: skip,
      limit: Limit,
      loadMore: true,
      filters: Filters,
    };

    getProducts(body);
    setSkip(skip);
  };

  function handleMouseover(e) {
    e.currentTarget.play();
  }

  function handleMouseout(e) {
    e.currentTarget.pause();
    e.currentTarget.currentTime = 0;
  }

  //  getProducts !
  useEffect(() => {
    let body = {
      skip: Skip,
      limit: Limit,
    };

    getProducts(body);
  }, []);

  //   getProducts !
  const showFilteredResults = (filters) => {
    let body = {
      skip: 0,
      limit: Limit,
      filters: filters,
    };

    getProducts(body);
    setSkip(0);
  };

  const handlePrice = (value) => {
    const data = price;
    let array = [];

    for (let key in data) {
      if (data[key]._id === parseInt(value, 10)) {
        array = data[key].array;
      }
    }
    return array;
  };

  const handleFilters = (filters, category) => {
    const newFilters = { ...Filters };

    newFilters[category] = filters;

    console.log("filters", filters);

    if (category === "price") {
      let priceValues = handlePrice(filters);
      newFilters[category] = priceValues;
    }
    showFilteredResults(newFilters);
    setFilters(newFilters);
  };

  //search  getProducts !
  const updateSearchTerm = (newSearchTerm) => {
    let body = {
      skip: 0, //DB   
      limit: Limit, //8
      filters: Filters, // genres price     + 
      searchTerm: newSearchTerm,
    };

    setSkip(0);

    //  SearchFeature   
    setSearchTerm(newSearchTerm);

    getProducts(body); //  !
  };

  const renderCards = Products.map((product, index) => {
    // var minutes = Math.floor(product.duration / 60);
    // var seconds = Math.floor(product.duration - minutes * 60);

    return (
      <Col lg={6} md={12} xs={24} key={index}>
        <div
          style={{
            overflow: "hidden",
            backgroundColor: "black",
            width: "100%",
            height: "0px",
            paddingTop: "56.25%",
            position: "relative",
          }}
        >
          <a href={`/product/${product._id}`}>
            <video
              style={{
                width: "100%",
                height: "100%",
                position: "absolute",
                top: "0",
                left: "0",
              }}
              src={`${product.filePath}`}
              onMouseOver={handleMouseover}
              onMouseOut={handleMouseout}
              muted
            />
            {/* <div className="duration">
                <span>{minutes} : {seconds}</span>
            </div> */}
          </a>
        </div>
        <br />
        <Meta
          avatar={
            <a href={`/videos/${product.writer._id}`}>
              <Avatar src={product.writer.image} />
            </a>
          }
          title={product.title}
        />
        <a href={`/videos/${product.writer._id}`}>
          <span>{product.writer.name}</span>
        </a>
        <span
          style={{
            marginRight: "10px",
            fontWeight: "500",
            float: "right",
            display: "inline-block",
            textAlign: "right",
          }}
        >{`${product.price.toLocaleString("ko-KR")} `}</span>
        <br />
      </Col>
    );
  });

  return (
    <div>
      <div>
        <h1>props.match.params.userId videos</h1>
        <h2> login_id : {props.user.userData && props.user.userData._id}</h2>
        <h2> query_id : {userId} </h2>
      </div>

      <div style={{ width: "75%", margin: "3rem auto" }}>
        {/* Filter */}

        <Row gutter={[16, 16]}>
          <Col lg={12} xs={24}>
            {/* CheckBox */}
            <Checkbox
              list={genres}
              handleFilters={(filters) => handleFilters(filters, "genres")}
            />
          </Col>
          <Col lg={12} xs={24}>
            {/* RadioBox */}
            <Radiobox
              list={price}
              handleFilters={(filters) => handleFilters(filters, "price")}
            />
          </Col>
        </Row>

        {/* Search */}

        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            margin: "1rem auto",
          }}
        >
          <SearchFeature refreshFunction={updateSearchTerm} />
        </div>

        {/* Cards */}

        <Row gutter={[16, 16]}>{renderCards}</Row>

        <br />

        {PostSize >= Limit && (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <button onClick={loadMoreHanlder}></button>
          </div>
        )}
      </div>
    </div>
  );
}

export default UserVideos;
