import React, { useState, useEffect } from 'react';
import jQuery from 'jquery';
import axios from "axios";
  
function Payment(props) {
  const [MerchantId, setMerchantId] = useState('')
  const [OrderName, setOrderName] = useState('')
  const [PG, setPG] = useState('')
  const [PayMethod, setPayMethod] = useState('')
  const [Amount, setAmount] = useState(0)
  const [BuyerTel, setBuyerTel] = useState('')
  const [BuyerName, setBuyerName] = useState('')
  const [BuyerEmail, setBuyerEmail] = useState('')
  const [Saved, setSaved] = useState(false)

  //    (Saved===true),    
  useEffect(()=>{
    if(Saved === true){
      /* 1.   */
      var IMP = window.IMP;
      IMP.init('imp26939383');

      /* 2.    */
      const param = {
        pg: PG, // PG
        pay_method: PayMethod, // 
        merchant_uid: MerchantId, // 
        amount: Amount,  // 
        name: OrderName, // 
        buyer_name: BuyerName, //  
        buyer_tel: BuyerTel, //  
        buyer_email: BuyerEmail, //  

        //     URL(imp_uid, merchant_uid, success  )
        m_redirect_url: "http://bringcon.shop:3000/payments/complete/mobile"
      };

      /* 4.    */
      IMP.request_pay(param, callback);
    }
    setSaved(false);
  }, [Saved])
  

  const saveOrder = (data) => {
    //   (Product._id)
    const dataToSave = {
      pg: data.pg,
      payMethod: data.payMethod,
      amount: data.total,
      buyerId: data.userData._id,
      buyerName: data.userData.name,
      buyerEmail: data.userData.email
    }

     axios.post('/api/users/order', dataToSave)
        .then(response => {
            if (response.data.success) {
                setMerchantId(response.data.merchantId)
                setOrderName(response.data.orderName)
                setPG(response.data.pg)
                setPayMethod(response.data.payMethod)
                setAmount(response.data.amount)
                setBuyerTel(response.data.buyerTel)
                setBuyerName(response.data.buyerName)
                setBuyerEmail(response.data.buyerEmail)
                setSaved(true)
            } else {
                alert("    .")
            }
        })
  }

  
  function onClickPayment(e) {
    e.preventDefault();

    //       DB 
    saveOrder(props)
  }

  function onSuccess (data) {
    alert(' .');
    props.onSuccess(data)
  }

  /* 3.    */
  function callback(rsp) {
    if (rsp.success) { //   :       
      alert(' ');
      // jQuery HTTP 
      jQuery.ajax({
        url: '/api/payments/complete', // 
        method: "POST",
        dataType: "json",
        headers: { "Content-Type": "application/json" },
        data: JSON.stringify({
          imp_uid: rsp.imp_uid, //   -      
          merchant_uid: rsp.merchant_uid // DB    ->   
          //      
        }),
        error: function(e) {
          console.log(e);
        }  
      })
      .done(function (data) { //  
        //step6   
        //   API  
        switch(data.status) {
          case "vbankIssued":
            //    
            break;
          case "success":
            //    
            onSuccess(data.payment);
            break;
        }
      })

    } else {
      alert(` : ${rsp.error_msg}`);
    }
  }

  return (
    <button onClick={onClickPayment}></button>
  );
}

export default Payment
