import React, { useState } from "react";
import { Form, Input } from "antd";
import Axios from "axios";
import { useSelector } from "react-redux";
const { TextArea } = Input;

const Banks = [
  { key: 1, value: "NH" },
  { key: 2, value: "KB" },
  { key: 3, value: "" },
  { key: 4, value: "" },
  { key: 5, value: "" },
  { key: 6, value: "IBK" },
  { key: 7, value: "SC" },
  { key: 8, value: "" },
  { key: 9, value: "KDB" },
  { key: 10, value: "SBI" },
  { key: 11, value: "" },
  { key: 12, value: "" },
  { key: 13, value: "" },
  { key: 14, value: "" },
  { key: 15, value: "" },
  { key: 16, value: "" },
  { key: 17, value: "" },
  { key: 18, value: "" },
  { key: 19, value: "" },
  { key: 20, value: "" },
  { key: 21, value: "" },
  { key: 22, value: "" },
  { key: 23, value: "" },
  { key: 24, value: "" },
  { key: 25, value: "" },
  { key: 26, value: "HSBC" },
  { key: 27, value: "" },
  { key: 28, value: "JP" },
  { key: 29, value: "" },
  { key: 30, value: "BNP" },
  { key: 31, value: "BOA" },
  { key: 32, value: "" },
];

function AddAccount(props) {
  const user = useSelector((state) => state.user);
  const [Bank, setBank] = useState(1);
  const [Number, setNumber] = useState();
  const [Holder, setHolder] = useState("");

  const BankChangeHandler = (event) => {
    setBank(event.currentTarget.value);
  };

  const NumberChangeHandler = (event) => {
    setNumber(event.currentTarget.value);
  };

  const HolderChangeHandler = (event) => {
    setHolder(event.currentTarget.value);
  };

  const submitHandler = (event) => {
    event.preventDefault();
    if (!Bank || !Holder || !Number === 0) {
      return alert("    .");
    }

    //   request .

    const body = {
      //   ID
      _id: user.userData._id,
      accountHolder: Holder,
      accountNumber: Number,
      Bank: Banks[Bank].value,
    };

    Axios.post("/api/users/account", body).then((response) => {
      if (response.data.success) {
        alert("   .");
        props.history.push("/");
      } else {
        alert("   .");
      }
    });
  };

  return (
    <div style={{ maxWidth: "1000px", margin: "2rem auto" }}>
      <div style={{ textAlign: "center", marginBottom: "2rem" }}>
        <h2>    </h2>
      </div>

      <Form onSubmit={submitHandler}>
        <div style={{ justifyContent: "space-between" }}>
          <label></label>
          <Input onChange={HolderChangeHandler} value={Holder} />
          <br />
          <br />
          <label></label>
          <Input type="number" onChange={NumberChangeHandler} value={Number} />
          <br />
          <br />
          <label> : </label>
          <select onChange={BankChangeHandler} value={Bank}>
            {Banks.map((item) => (
              <option key={item.key} value={item.key}>
                {" "}
                {item.value}
              </option>
            ))}
          </select>
          <br />
          <br />
        </div>
        <button type="submit"></button>

        <br />
        <div>
          [ 15 9]   ,   ,
            ,    ,   
                   
            .
        </div>
      </Form>
    </div>
  );
}

export default AddAccount;
