import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { getCartItems, removeCartItem, onSuccessBuy } from '../../../_actions/user_actions';
import UserCardBlock from './Sections/UserCardBlock';
import { Empty, Result } from 'antd';
import Payment from '../../utils/Payment';

function CartPage(props) {
    const dispatch = useDispatch();

    const [Total, setTotal] = useState(0)
    const [ShowTotal, setShowTotal] = useState(false)
    const [ShowSuccess, setShowSuccess] = useState(false)
    const [PG, setPG] = useState('html5_inicis')
    const [PayMethod, setPayMethod] = useState('card')

    useEffect(() => {

        let cartItems = []
        // User state cart     
        if (props.user.userData && props.user.userData.cart) {
            if (props.user.userData.cart.length > 0) {
                props.user.userData.cart.forEach(item => {
                    cartItems.push(item.id)
                })
                dispatch(getCartItems(cartItems, props.user.userData.cart))
                    .then(response => { calculateTotal(response.payload) })
            }
        }
    }, [props.user.userData, dispatch])


    let calculateTotal = (cartDetail) => {
        let total = 0;

        cartDetail.map(item => {
            total += parseInt(item.price, 10) * item.quantity
        })

        setTotal(total)
        setShowTotal(true)

    }


    let removeFromCart = (productId) => {

        dispatch(removeCartItem(productId))
            .then(response => {

                if (response.payload.productInfo.length <= 0) {
                    setShowTotal(false)
                }

            })

    }

    const transactionSuccess = (data) => { // Payment data 
        //onSuccessBuy action !
        dispatch(onSuccessBuy({
            paymentData: data, //  Payment    
            cartDetail: props.user.cartDetail //    CartDetail  
        }))
            .then(response => { //  
                if (response.payload.success) {
                    setShowTotal(false)
                    setShowSuccess(true)
                }
            })
    }

    //  
    const handleOptionChange = (changeEvent) => {
        setPayMethod(changeEvent.target.value)
    }


    return (
        <div style={{ width: '85%', margin: '3rem auto' }}>
            <h1>My Cart</h1>

            <div>
                <UserCardBlock products={props.user.cartDetail} removeItem={removeFromCart} />
            </div>

            {ShowTotal ?
                <div style={{ marginTop: '3rem' }}>
                    <h2>Total Amount: ${Total}</h2>
                </div>
                : ShowSuccess ?
                    //     
                    <Result
                        status="success"
                        title="Successfully Purchased Items"
                    />
                    :
                    <>
                        <br />
                        <Empty description={false} />
                    </>
            }


            {/* ShowTotal     
                   */}
            {ShowTotal &&
                <div>
                    <form>
                        <div className="form-check">
                            <label>
                            <input
                                type="radio"
                                name="react-tips"
                                value="card"
                                checked={PayMethod === "card"}
                                onChange={handleOptionChange}
                                className="form-check-input"
                            />
                             
                            </label>
                        </div>
                        
                        <div className="form-check">
                            <label>
                            <input
                                type="radio"
                                name="react-tips"
                                value="trans"
                                checked={PayMethod === "trans"}
                                onChange={handleOptionChange}
                                className="form-check-input"
                            />
                             
                            </label>
                        </div>
                        
                        <div className="form-check">
                            <label>
                            <input
                                type="radio"
                                name="react-tips"
                                value="phone"
                                checked={PayMethod === "phone"}
                                onChange={handleOptionChange}
                                className="form-check-input"
                            />
                             
                            </label>
                        </div>
                    
                        <div className="form-group">
                            <Payment
                                pg={PG}
                                payMethod={PayMethod}
                                total={Total}
                                userData={props.user.userData}
                                onSuccess={transactionSuccess}
                            />
                        </div>
                    </form>
                </div>
            }
        </div>
      ) 
}

export default CartPage;
